.dark-mode{
    // Select
    .select select{
        option{
            color: $dark2;
        }
    }
    // Swiper Theme
    .swiper-pagination-bullet {
        &::before{
            background: $c-white;
        }
    }

    // Featured Technology
    #featured-technology .banner{
        background-color: $dark3;
    }

    // Header
    .f-input {
        background-color: $dark3;
        input, ::placeholder{
            color: rgba($c-white, .5);
        }
    }
    &.page-type-cover.header-opaque, &:not(.page-type-cover){
        .header-top{
            background-color: $dark2;
            color: $c-text-light;
            >.container>.row{
                border-bottom-color: $dark3;
            }
            .logo-org{
                display: none;
            }
            .logo-alt{
                display: inline-block;
            }
        }
    }
    // Featured Technologies
    .bnr-icon .bnr-footer::after{
        filter: unset;
    }
    // Buttons
    .btn {
        &-outline {
            &-dark {
                border-color: $c-white;
                color: $c-white;
                &.selected {
                    background-color: $c-white;
                    color: $c-dark;
                }
            }
        }
    }
    // Modal
    .modal{
        &-header{
            border-color: $dark4;
        }
        &-footer{
            background-color: $dark4;
        }
    }
    // Accordion
    .acc-item{
        border-color: $dark4;
    }
    // Tabs
    .nav-tabs{
        &.tab-alt .nav-link.active{
            color: $c-text-light;
        }
        &:not(.tab-alt){
            box-shadow: inset 0px -3px 0px 0px $dark6;
        }
    }
    .tab-holder:not(.tab-ext-colored){
        .nav-tabs{
            &.nav-tabs.tab-ext .nav-link.active{
                background-color: $dark2;
            }
        }
    }
    // PDP
    .pdp-tab{
        .acc-item, .accordion > li{
            background-color: $dark1;
        }
    }
    .ftc-bg-light{
        .ftc-text{
            color: $c-text-grey !important;
        }
    }
    .cmp-item, .compare-primary-features{
        background-color: $dark2;
    }
    .compare-primary-features{
        .item{
            + .item{
                border-top-color: $c-grey-lighter;
            }
        }
    }
    .compare-holder.title-block .compare-top-panel{
        background-color: $c-bg-light;
    }
    .promo{
        &-holder{
            background-color: $dark1;
        }
        &-header{
            background-color: $dark2;
        }
    }
    &.pdp-purchase-ready .pdp .pdp-purchase-fixed{
        background-color: $dark3;
    }
    // Support
    .spt-search-input{
        background-color: $dark3;
    }
    .srv-item{
        background-color: $dark2;
    }
    // Login & Signup
    .sgn-holder{
        background-color: $dark1;
    }
    // User Orders
    .ord-grid.lp{
        background-color: $dark2;
    }
    // Cart
    .cart{
        &-row{
            border-color: $dark4;
            background-color: $dark2;
            &-set + .cart-row-set{
                border-color: $dark4;
            }
            &-promos .nav-tabs.tab-ext{
                background-color: $dark1;
                padding-top: 10px;
            }
        }
        &-extra{
            background-color: $dark1;
        }
    }
    .club-coupons-list .cpn-grid{
        background-color: $dark2;
        border-color: $dark4;
    }
    .prd-mini.prd-mini-promotion, .prd-mini.prd-mini-cart{
        .prd-mini-detail{
            border-color: $dark4;
        }
    }
    // Home
    #miracle-flavors .events{
        background-color: $dark3;
    }
    // Product
    .prd-detail .btn{
        @extend .btn-light;
    }
}