// Dark Mode Colors
$dark0: #000000;
$dark1: #111111;
$dark2: #222222;
$dark3: #333333;
$dark4: #444444;
$dark5: #555555;
$dark6: #666666;

// Banner Background & Text Colours
$c-banner: (
    1: (
        background: $dark2,
        color: $c-text-light
    ),
    2: (
        background: #454c5b,
        color: $c-text-light
    ),
    3: (
        background: $c-primary,
        color: $c-text-light
    ),
    4: (
        background: #514b4b,
        color: $c-text-light
    ),
    5: (
        background: #d1d2d5
    ),
    6: (
        background: #bbb2b0
    ),
    7: (
        background: $c-bg-colored
    ),
    8: (
        background: #735858,
        color: $c-text-light
    ),
    9: (
        background: #4a4a4a,
        color: $c-text-light
    ),
    10: (
        background: #b32424,
        color: $c-text-light
    ),
    11: (
        background: $c-dark,
        color: $c-text-light
    )
);

// Colours
$c-brand: #ff0000;
$c-primary: #d93131;
$c-secondary: #222222;
$c-light: #f6f6f6;
$c-dark: #222222;
$c-black: #000000;
$c-white: #ffffff;

$c-grey-dark: #989898;
$c-grey-light: #d8d8d8;
$c-grey-lighter: #e5e5e5;

$c-grey-border: $dark4;
$c-grey-border-dark: #b7b7b7;
$c-pdp-border: #444444;
// $c-colored-border: #dccbd7;
$c-colored-border: #EDE5EA;
$c-assistant-border: #d6c8d1;

$c-text-dark: #ffffff;
$c-text-light: #ffffff;
$c-text-weak: #7f7f7f;
$body-color: $c-text-dark;

$c-bg-body: $dark1;
$c-bg-light: $dark2;
$c-bg-colored: #574c53;
$c-bg-dark: #111111;
$c-bg-prd-footer: $dark3;
$c-bg-prd-mini: $dark2;
$c-bg-pdp-details-1: #474747;
$c-bg-pdp-details-2: #222222;
$c-bg-assistant: $c-bg-colored;

$c-success: #6e994e;
$c-error: #d93131;
$c-warning: #ffce00;

$c-bullet-dark: #000000;
$c-bullet-light: #ffffff;

$menu-sub-bg: $dark0;
$menu-lv-1-border-color: $dark4;

// Dark Mode Controls
$dm-control-height: 340px;